.header{
    width: 100vw;
    height: 100px;
    border-bottom: 3px solid #fff;
    z-index: 2;
    display: flex;
    background-color: black;
}
.header--title{
    color: #fff;
    margin: auto 20px;
    font-size: 3rem;
}
.header--li{
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: auto 70px auto auto;
    font-family: 'Satisfy', cursive;
}
.header--ul{
    font-size: 1.8rem;
}
.header--ul > a{
    text-decoration: none;
    color: #fff;
}
.header--name{
    color: #fff;
    margin: auto 0;
    align-self: flex-end;
}