.history--container{
    width: 100vw;
    margin: 0 auto;
}
.history--links__container{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    margin-top: auto;
}
.history--links__container > a {
    margin: 0 auto;
    font-size: 1.5rem;
}
.corazon{
    height: 800px;
    width: 100%;
    padding: 100px;
}
.dragon{
    height: 800px;
    width: 100%;
    padding: 100px; 
}
.mort{
    height: 800px;
    width: 100%;
    padding: 100px;
}
.history--text__container > h1{
    margin: 0;
    font-family: 'Satisfy', cursive;
}
.history--text__container > p{
    font-size: 1.1rem;
    line-height: 30px;
}
.history--text__container{
    width: 50%;    
    min-height: 100%;
    margin: 0 auto;
    color: #fff;
    background-color: #000;
    display: flex;
    flex-direction: column;
    padding: 20px;
}
.poe--container{
    text-align: center;
}
.reference--button{
    border: none;
    background: transparent;
    color: #3992ff;
    cursor: pointer;
    width: fit-content;
    font-size: 1rem;
    text-decoration: underline;
}