.home--container{
    z-index: 2;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}
.home--welcome__container{
    text-align: center;
}
.home--image__container{
    z-index: 2;
    display: flex;
    justify-content: center;
}
.home--image__container > img{
    width: 50%;
    height: 50%;
    border: 2px solid #fff;
    border-radius: 25px;
    align-self: center;
}
.home--title{
    margin: 200px 0;
    font-size: 4rem;
}
.home--histories__container{
    display: flex;
    width: 100%;
    flex-wrap: nowrap;
    justify-content: center;
}
.home--history__container{
    text-align: center;
    width: 100%;
    height: 400px;
    border: 1px solid #000;
    border-radius: 25px;
    cursor: pointer;
    margin: 30px;
    font-family: 'Satisfy', cursive;
    font-size: 1.5rem;
}
.first{
    box-shadow: 10px 10px 163px 51px rgba(152, 0, 0,0.75);
    -webkit-box-shadow: 10px 10px 163px 51px rgba(152, 0, 0,0.75);
    -moz-box-shadow: 10px 10px 163px 51px rgba(152, 0, 0,0.75);
    background-image: url('https://cdn.discordapp.com/attachments/922917249919098971/1039585244552101999/605919f129d5a8162b3c07ef7195f4de_copy.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: rgb(152, 0, 0);
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.second{
    box-shadow: 10px 10px 163px 51px rgba(255,255,255,0.75);
    -webkit-box-shadow: 10px 10px 163px 51px rgba(255,255,255,0.75);
    -moz-box-shadow: 10px 10px 163px 51px rgba(255,255,255,0.75);
    background-image: url('https://cdn.discordapp.com/attachments/922917249919098971/1039585244862496899/ee49cf6e694b241d84b940e7ce925926.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: rgb(255,255,255);
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.third{
    box-shadow: 10px 10px 163px 51px rgba(9, 48, 154,0.75);
    -webkit-box-shadow: 10px 10px 163px 51px rgba(9, 48, 154,0.75);
    -moz-box-shadow: 10px 10px 163px 51px rgba(9, 48, 154,0.75);
    background-image: url('https://cdn.discordapp.com/attachments/922917249919098971/1039585244203991170/1b00d201cdb95c7c6a460440c18d30c4_copy.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: rgb(9, 48, 154);
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.fourth{
    border: #fff 2px solid;
    border-radius: 0%;
}
.home--link{
    text-align: center;
    width: 100%;
    height: 400px;
    border-radius: 25px;
    cursor: pointer;
    font-family: 'Satisfy', cursive;
    margin: 0 30px 50px 30px;
    text-decoration: none;
}